.profile{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 0;
    margin: 0;
    height: 100vh;
    border-left: 1px solid rgb(250, 250, 250);
}

.profile .user-image{
    width: 50%;
}

.profile .battle-stats{
    display: flex;
    justify-content: space-between;
    
}

.profile .battle-stats h4{
    padding-right:1em;
}